.Controls {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    width: 90%;
    height: 100%;
    margin: 5px auto;
    border: 3px solid steelblue;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.Controls h1 {
    text-align: center;
    margin-bottom: 0;
}

.Controls button {
    width: 35px;
    background-color: #ebe8e2;
    margin: 0 auto;
}

.Controls button:hover {
    border: 3px solid steelblue;
    color: steelblue;
}

.Controls .Reset {
    border: 1px solid black;
    font-weight: bold;
    font-size: 15px;
    height: 50px;
    width: 100px;
    margin-bottom: 25px;
    outline: none;
}

.Controls .Reset:active {
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
            transform: scale(0.9);
}

.MoneyControls {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-flow: column wrap;
        flex-flow: column wrap;
    margin: 0 auto;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 300px;
    width: 90%;
}

@media screen and (max-width: 650px) {
    .Controls {
        height: 350px;
    }
    
    .Controls h1 {
        font-size: 22px;
    }

    .Controls button {
        width: 25px;
    }

    .Controls .Reset {
        width: 75px;
        height: 50px;
    }
}



