  .bill {
    margin: 2px;
    border: 1px solid darkgreen;
    height: 50px;
    width: 100px;
    background-color: #85bb65;
    font-size: 25px;
    font-weight: bold;
    color: darkgreen;
  }

  .hundred::after {
    content: '$100';
    display: block;
    text-align: center;
    margin-top: 10px;
  }
  
  .fifty::after {
    content: '$50';
    display: block;
    text-align: center;
    padding-top: 10px;
  } 

  .twenty::after {
    content: '$20';
    display: block;
    text-align: center;
    padding-top: 10px;
  }
  
  .ten::after {
    content: '$10';
    display: block;
    text-align: center;
    padding-top: 10px;
  }

  .five::after {
    content: '$5';
    display: block;
    text-align: center;
    padding-top: 10px;
  }

  .dollar::after {
    content: '$1';
    display: block;
    text-align: center;
    padding-top: 10px;
  }

  .coin {
    margin: 2px;
    font-weight: bold;
  }
  
  .quarter {
    width: 75px;
    height: 75px;
    font-size: 22px;
    font-weight: bold;
    background-color: gray;
    border: 1px solid darkgray;
    border-radius: 50%;
  }
  
  .quarter::after {
    content: '25c';
    display: block;
    text-align: center;
    color: white;
    padding-top: 25px;
  }
  
  .dime {
    width: 60px;
    height: 60px;
    font-size: 18px;
    background-color: #747678;
    border-radius: 50%;
  }
  
  .dime::after {
    content: '10c';
    display: block;
    text-align: center;
    color: white;
    padding-top: 20px;
  }
  
  .nickel {
    width: 50px;
    height: 50px;
    background-color: #BDBAAE;
    border-radius: 50%;
  }
  
  .nickel::after {
    content: '5c';
    display: block;
    text-align: center;
    color: white;
    padding-top: 15px;
  }
  
  .penny {
    width: 40px;
    height: 40px;
    background-color: #B87333;
    border-radius: 50%;
  }
  
  .penny::after {
    content: '1c';
    display: block;
    text-align: center;
    color: white;
    padding-top: 10px;
  }


